<template>
    <div id="search_result_vue">
      <van-nav-bar
        :title="preparedTitle"
        left-text="返回"
        left-arrow
        @click-left="$router.go(-1)"
        />
        <van-dropdown-menu active-color="#1989fa" >
          <van-dropdown-item v-model="value1" :options="option1" @change="onChange"/>
          <van-dropdown-item v-model="value2" :options="option2" />
        </van-dropdown-menu>
        <div class="passages" v-show="value1==='passages'">
          <div class="result">
          <articleCard
          v-for="article in passages"
          :key="article.articleId"
          :article="article"
          :show-edit="false"
          ></articleCard>
          </div>
          <van-empty description="空空如也" v-show="passages===null">
            <van-button round type="danger" class="bottom-button">再搜索一个</van-button>
          </van-empty>
        </div>
        <div class="creators" v-show="value1==='creators'">
          <div class="result">
            <UserProfile
            v-for="user in creators"
            :key="user.userId"
            :user="user"
            :enable-link="true"
            />
          </div>
          <van-empty description="空空如也" v-show="creators.length===0">
            <van-button round type="danger" class="bottom-button">再搜索一个</van-button>
          </van-empty>
        </div>
    </div>
  </template>
<script>
import { getArticlesByTitle } from '@/api/article'
import { getUsersByUsername } from '@/api/user'
import articleCard from '@/components/articleCard.vue'
import UserProfile from '@/components/UserProfile.vue'
export default {
  name: 'search_result_vue',
  components: {
    articleCard,
    UserProfile
  },
  data: () => {
    return {
      preparedTitle: '',
      passages: [],
      creators: [],
      value1: 'passages',
      value2: 'a',
      option1: [
        { text: '文章', value: 'passages' },
        { text: '创作者', value: 'creators' }
      ],
      option2: [
        { text: '默认排序', value: 'a' },
        { text: '热度排序', value: 'b' }
      ]
    }
  },
  async created () {
    this.preparedTitle = `与"${this.$route.query.keyword}"相关的结果`
    const res1 = await getArticlesByTitle(this.$route.query.keyword)
    this.passages = res1.data
    const res2 = await getUsersByUsername(this.$route.query.keyword)
    this.creators = res2.data
  },
  methods: {
    onChange (value) {
    }
  }
}
</script>
  <style scoped>

  </style>
